import requestUtil from '@/utils/request'

// 获取图形验证码
export const getCaptchaApi = () => {
  return requestUtil.get('/captcha/image')
}

// 登录-获取短信验证码
export const getMsgCodeApi = (captchaCode, captchaKey, mobile) => {
  return requestUtil({
    method: 'post',
    url: '/captcha/sendSmsCaptcha',
    data: {
      form: {
        captchaCode,
        captchaKey,
        mobile
      }
    }
  })
}

// 手机验证码登录
export const userLoginApi = (smsCode, mobile) => {
  return requestUtil.post('/passport/login', {
    form: {
      smsCode,
      mobile,
      isParty: false,
      partyData: {}
    }
  })
}
