<template>
  <div class="login">
    <van-nav-bar title="登录" left-arrow @click-left="$router.back()" />
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input
            class="inp"
            maxlength="11"
            placeholder="请输入手机号码"
            type="text"
            v-model="mobile"
          />
        </div>
        <div class="form-item">
          <input
            class="inp"
            maxlength="5"
            placeholder="请输入图形验证码"
            type="text"
            v-model="captchaCode"
          />
          <img v-if="captchaUrl" :src="captchaUrl" alt="" @click="getCaptcha" />
        </div>
        <div class="form-item">
          <input
            class="inp"
            placeholder="请输入短信验证码"
            type="text"
            v-model="smsCode"
          />
          <button @click="getMsgCode">
            {{ msgTimer ? secondCounting + "秒后重新获取" : "获取验证码" }}
          </button>
        </div>
      </div>

      <div class="login-btn" @click="userLogin">登录</div>
    </div>
  </div>
</template>

<script>
import { getCaptchaApi, getMsgCodeApi, userLoginApi } from '@/api/login'

export default {
  name: 'LoginIndex',

  data () {
    return {
      captchaUrl: '',
      captchaKey: '',
      msgTimer: null,
      secondLimit: 10,
      secondCounting: 10,
      mobile: '',
      captchaCode: '',
      smsCode: ''
    }
  },

  methods: {
    async getCaptcha () {
      const res = await getCaptchaApi()
      // console.log(res)
      const { data: { base64, key } } = res
      this.captchaUrl = base64
      this.captchaKey = key
    },

    verifyLogin () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.captchaCode)) {
        this.$toast('请输入正确的图形验证码')
        return false
      }
      return true
    },

    async getMsgCode () {
      if (!this.verifyLogin()) {
        return
      }
      if (!this.msgTimer) {
        await getMsgCodeApi(this.captchaCode, this.captchaKey, this.mobile)
        this.$toast('验证码：246810')
        this.msgTimer = setInterval(() => {
          this.secondCounting--
          // console.log(this.msgTimer, this.secondCounting)
          if (this.secondCounting <= 0) {
            clearInterval(this.msgTimer)
            this.msgTimer = null
            this.secondCounting = this.secondLimit
          }
        }, 1000)
      }
    },

    async userLogin () {
      if (!this.verifyLogin()) return
      if (!/^\d{6}$/.test(this.smsCode)) {
        this.$toast('手机验证码为6位数字')
        return
      }
      const res = await userLoginApi(this.smsCode, this.mobile)
      this.$toast('登录成功')
      this.$store.commit('user/setUserInfo', res.data)
      setTimeout(() => {
        this.$router.replace(this.$route.query.backUrl || '/user')
      }, 1000)
    }

  },

  created () {
    this.getCaptcha()
  },

  destroyed () {
    clearInterval(this.msgTimer)
  }

}
</script>

<style lang="less" scoped>
.container {
  padding: 48px 30px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg, #ecb53c, #ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
